import { FC } from 'react';
import cN from 'classnames';
import { NavLink } from 'react-router-dom';

type Props = {
  to: string,
  text: string
};

export const PageNavLink: FC<Props> = ({ to, text }) => {
  return (
    <NavLink
      className={({ isActive }) => cN(
        'navbar-item', { 'has-background-grey-lighter': isActive },
      )}
      to={to}
    >
      {text}
    </NavLink>
  );
};
